"use client";

import { useState } from "react";
import Link from "next/link";
import { toast } from "sonner";
import { Mail, ArrowLeft } from "lucide-react";

export default function ForgotPasswordPage() {
  const [email, setEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [sent, setSent] = useState(false);

  async function onSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!email.trim()) {
      toast.error("Informe um email válido.");
      return;
    }

    setLoading(true);
    try {
      const res = await fetch("/api/auth/forgot-password", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email }),
      });

      const json = await res.json().catch(() => ({}));

      // ⚠️ Mesmo que o email não exista, o endpoint retorna success:true
      if (!res.ok) {
        throw new Error(json?.error || "Erro ao solicitar recuperação.");
      }

      setSent(true);
      toast.success("Se esse email existir, enviamos um link de redefinição.");
    } catch (err: any) {
      toast.error(err?.message || "Erro ao solicitar recuperação.");
    } finally {
      setLoading(false);
    }
  }

  return (
    <main className="min-h-screen w-full flex items-center justify-center px-4">
      <div className="w-full max-w-md rounded-2xl border border-white/10 bg-[#121212] p-8 shadow-xl">
        <div className="mb-6">
          <div className="flex items-center justify-center gap-2">
            <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-[#00FF8B] to-[#007B5F] flex items-center justify-center">
              <Mail className="w-5 h-5 text-black" />
            </div>
          </div>

          <h1 className="mt-4 text-2xl font-semibold text-center">
            Recuperar senha
          </h1>

          <p className="mt-2 text-sm text-zinc-400 text-center">
            Informe seu email para receber o link de redefinição.
          </p>
        </div>

        {!sent ? (
          <form onSubmit={onSubmit} className="space-y-4">
            <div>
              <label className="text-sm text-zinc-300">Email</label>
              <input
                type="email"
                placeholder="seuemail@exemplo.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="mt-2 w-full h-11 rounded-lg bg-[#1A1A1A] border border-white/10 px-4 text-sm focus:outline-none focus:ring-2 focus:ring-[#00FF8B]"
              />
            </div>

            <button
              type="submit"
              disabled={loading}
              className="w-full h-11 rounded-lg bg-[#00FF8B] text-black font-medium hover:opacity-90 transition disabled:opacity-60"
            >
              {loading ? "Enviando..." : "Enviar link"}
            </button>
          </form>
        ) : (
          <div className="rounded-xl border border-[#00FF8B]/20 bg-[#0D0D0D] p-4">
            <p className="text-sm text-zinc-300">
              Pronto! Se esse email estiver cadastrado, o link foi gerado.
            </p>
            <p className="mt-2 text-xs text-zinc-400">
              Em desenvolvimento, o link aparece no terminal do Next.
            </p>

            <button
              onClick={() => {
                setSent(false);
                setEmail("");
              }}
              className="mt-4 w-full h-11 rounded-lg border border-white/10 bg-[#1A1A1A] hover:bg-[#252525] transition"
            >
              Tentar outro email
            </button>
          </div>
        )}

        <div className="mt-6 text-center">
          <Link
            href="/login"
            className="inline-flex items-center justify-center gap-2 text-sm text-[#00FF8B] hover:underline"
          >
            <ArrowLeft className="w-4 h-4" />
            Voltar para login
          </Link>
        </div>
      </div>
    </main>
  );
}
